import React from 'react';
import {connect} from 'react-redux';
import {Button, Grid} from '@material-ui/core';

import MaterialTable from 'material-table';
import moment from 'moment';

import Constants from '../../../../../utils/Constants';

import {
  getAllHistoricalConsultsRequest
} from '../../../../Consult/ConsultApiActions';

class HistoricalConsults extends React.Component{

    constructor(props) {
      super(props);
      this.paginationParams = {
        page: 0,
        rowsPerPage: Constants.ROW_PER_PAGE_5,
      };
      this.state = {
        paginationParams: Object.assign({}, this.paginationParams)
      }
    };

    componentDidMount = () => {
      let data = this.state.paginationParams;
      this.props.getAllHistoricalConsults(data)
    };

    viewConsult = (consultId) => {
      sessionStorage.setItem('consultHistory', consultId);
      window.location = '/consultHistory';
    };

    render(){
          const { historicalConsults } = this.props;
          const { paginationParams } = this.state;

          const columns = [{
            title: 'Name',
            fiels: 'name',
            filterable: false,
            sorting: false,
            render: props => {
              return (
                <span>
                  { props.practiceName?props.practiceName:"-" }
                </span>
              )
            }
          },
          {
            title: 'Consult Time',
            field: 'appointmentDate',
            filterable: false,
            sorting: false,
            render: props => {
              let displayDate = moment(props.appointmentDate).format("dddd,  DD MMMM");
              let startTime = moment(props.appointmentTime).format("h:mm A");
              let endTime = moment(props.appointmentTime).add(15, 'minutes').format("h:mm A");
              displayDate = displayDate+" ("+startTime+" - "+endTime+")";
              return (
                <span>
                  { displayDate }
                </span>
              )
            }
          },
          ];

          let isDoctor = this.props.loggedInUser.Roles.find((role) => role.name===Constants.SYSTEM_ROLES.ROLE_DOCTOR);
          if(isDoctor){
            columns.push({
              title: 'Action',
              maxWidth: 150,
              filterable: false,
              sorting: false,
              field: 'id',
              sortable: false,
              render: props => {
                return (
                  <div>
                    {
                      <Button color="primary" onClick={() => this.viewConsult(props.id)}>View</Button>
                    }
                  </div>
                )
              }
            })
          }
        return(
            <Grid container spacing={3}>
              <Grid item xs={12} className="informationHeader titleFontSize practiceOwnerInformation">
                Historical Consults
              </Grid>
              <Grid item xs={12}>
              <MaterialTable
                    title=""
                    scrollable
                    columns={columns}
                    data={historicalConsults.rows}
                    options={{
                      search: false,
                      pageSizeOptions: [10, 20, 30, 40, 50],
                      pageSize: paginationParams.rowsPerPage,
                      emptyRowsWhenPaging: false,
                      filtering: false,
                      headerStyle: {
                        padding: '2px 40px 2px 16px'
                      },
                      searchFieldStyle: {
                        padding: '2px 40px 2px 16px'
                      }
                    }}
                    pages={Math.ceil(historicalConsults.count / paginationParams.rowsPerPage)}
                    totalCount={historicalConsults.count}
                    page={paginationParams.page}
                    onChangeRowsPerPage={(pageSize) => {
                      let data = this.state.paginationParams;
                      data['rowsPerPage'] = pageSize;
                      this.setState({
                        paginationParams: data
                      }, () => {
                        this.props.getAllHistoricalConsults(data);
                      })
                    }}
                    onChangePage={(page) => {
                      let data = this.state.paginationParams;
                      data['page'] = page;
                      this.setState({
                        paginationParams: data
                      }, () => {
                        this.props.getAllHistoricalConsults(this.state.paginationParams);
                      })

                    }}
                  />
              </Grid>
            </Grid>
        )
    }

}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    historicalConsults: state.consults.historicalConsults,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllHistoricalConsults: (data) => {
      return dispatch(getAllHistoricalConsultsRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    }
  }
};

HistoricalConsults = connect(
  mapStateToProps,
  mapDispatchToProps
)(HistoricalConsults);

export default HistoricalConsults;
