import React from 'react';
import {connect} from 'react-redux';

import MaterialTable from 'material-table';
import {Grid, Dialog, MenuItem, DialogContent, Button, DialogTitle,
  Typography, IconButton} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import {
  MuiPickersUtilsProvider,
  DatePicker,
  TimePicker
} from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import moment from 'moment-timezone';

import { PlusCircleOutlined } from '@ant-design/icons';
import Constants from '../../../../../utils/Constants';
import ConstantMessages from '../../../../../utils/Strings';

import { ValidatorForm, TextValidator} from 'react-material-ui-form-validator';

import {
  getAllConsultsRequest,
  addConsultRequest,
  confirmConsultRequest,
  rejectConsultRequest
} from '../../../../Consult/ConsultApiActions';
