const dialogTitleStyles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});
const CustomeDialogTitle = withStyles(dialogTitleStyles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <DialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
});

class InvitePatientForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      patient:{
        name: '',
        email: '',
        appointmentDate: '',
        appointmentTime: '',
        chiefComplaint: ''
      },
      isFormSubmit: false,
      validAppointmentDate: false,
      validAppointmentTime: false,
    }
  }

  componentDidMount = () => {
    ValidatorForm.addValidationRule(ONLY_CHARACTER_ACCEPT, UtilHelper.validateString);
    ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
  };

  componentWillUnmount() {
    ValidatorForm.removeValidationRule(ONLY_CHARACTER_ACCEPT);
    ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
  }

  handleChange = (event) => {
    const patient = Object.assign({}, this.state.patient);
    patient[event.target.name] = event.target.value;
    this.setState({ patient });
  }

  handleDateChange = (date) => {
    const patient = Object.assign({}, this.state.patient);
    patient['appointmentDate'] = date;
    this.setState({ patient, validAppointmentDate: true });
  }

  handleTimeChange = (time) => {
    const patient = Object.assign({}, this.state.patient);
    patient['appointmentTime'] = time;
    this.setState({ patient, validAppointmentTime: true });
  }

  handleSubmit = () => {
    this.setState({ isFormSubmit: true }, () => {

      const patient = Object.assign({}, this.state.patient);
      this.setState({
        validAppointmentDate: patient.appointmentDate === ""?false:true,
        validAppointmentTime: patient.appointmentTime === ""?false:true
      }, () => {
        this.onCreate();
      })
    })
  };

  onCreate = () => {
    if(this.state.validAppointmentDate && this.state.validAppointmentTime){
      this.props.onCreate(this.state.patient);
    }
  }

  onCancel = () => {
    const patient={
      name: '',
      email: '',
      appointmentDate: '',
      appointmentTime: '',
      chiefComplaint: ''
    }

    this.setState({
      patient
    },()=>{
      this.props.onCancel();
    })

  }

  render() {
    let {visible} = this.props;
    const {patient} = this.state;

    return (
      <Dialog
        // fullScreen={fullScreen}
        open={visible}
        // onClose={this.onCancel}
        aria-labelledby="responsive-dialog-title"
      >
        <CustomeDialogTitle onClose={this.onCancel} id="responsive-dialog-title">Invite Patient</CustomeDialogTitle>
        <DialogContent>
          <ValidatorForm
            ref="form"
            onSubmit={() => this.handleSubmit()}
            noValidate
            onError={errors => console.log(errors)}
          >
            <Grid container spacing={0}>
              <Grid item xs={12}>
                <TextValidator
                  fullWidth
                  variant="outlined"
                  label="Name" onChange={this.handleChange}
                  name="name" value={patient.name || ''}
                  validators={['required', ONLY_BLANK_SPACES, ONLY_CHARACTER_ACCEPT, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD]}
                  errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.digit_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field]}
                />
              </Grid>
              <Grid item xs={12}>
                <TextValidator
                  fullWidth
                  className="marginTop10"
                  variant="outlined"
                  label="Email" onChange={this.handleChange}
                  name="email" value={patient.email || ''} validators={['required', 'isEmail']}
                  errorMessages={['this field is required', 'email is not valid']}
                />
              </Grid>
              <Grid container spacing={2}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <Grid item xs={6}>
                    <DatePicker
                      className="appointmentDate"
                      margin="normal"
                      inputVariant="outlined"
                      id="date-picker-dialog"
                      label="Appointment Date"
                      format="dd/MM/yyyy"
                      minDate={ moment(new Date()) }
                      value={patient.appointmentDate?patient.appointmentDate:null}
                      onChange={this.handleDateChange}
                    />
                    {
                      this.state.isFormSubmit && !this.state.validAppointmentDate ?
                        <div className="phone-error">Please select valid date</div> : ''
                    }
                  </Grid>
                  <Grid item xs={6}>
                    <TimePicker
                      margin="normal"
                      className="appointmentTime"
                      inputVariant="outlined"
                      id="time-picker"
                      label="Appointment Time"
                      value={patient.appointmentTime?patient.appointmentTime:null}
                      onChange={this.handleTimeChange}
                    />
                    {
                      this.state.isFormSubmit && !this.state.validAppointmentTime ?
                        <div className="phone-error">Please select valid time</div> : ''
                    }
                  </Grid>
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={12}>
                <TextValidator
                    fullWidth
                    variant="outlined"
                    multiline
                    rows={3}
                    label="Chief Complaint" onChange={this.handleChange}
                    name="chiefComplaint" value={patient.chiefComplaint || ''}
                    validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_AREA]}
                    errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_area]}
                  />
              </Grid>
              <Grid item xs={12}>
                <Button type="submit" variant="contained" color="primary"
                  className="invite-add-user-btn">Invite</Button>
              </Grid>
            </Grid>
          </ValidatorForm>
        </DialogContent>
      </Dialog>
    );
  }
}




class AcceptRejectConsultForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      isReject: false,
      consultData: {},
      selectedPreference: "",
      formData: {
        appointmentTime: '',
        reason: '',
        reasonText: ''
      },
      isFormSubmit: false,
      validAppointmentTime: true
    }
  }

  componentDidMount = () => {
    ValidatorForm.addValidationRule(ONLY_CHARACTER_ACCEPT, UtilHelper.validateString);
    ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
  };

  componentWillUnmount() {
    ValidatorForm.removeValidationRule(ONLY_CHARACTER_ACCEPT);
    ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
  }

  handleChange = (event) => {
    const formData = Object.assign({}, this.state.formData);
    formData[event.target.name] = event.target.value;
    this.setState({ formData });
  }

  handleTimeChange = (time) => {
    const formData = Object.assign({}, this.state.formData);
    formData['appointmentTime'] = time;
    this.setState({ formData });
  }

  handleAcceptConsult = () => {
    if(this.state.selectedPreference){
      const formData = Object.assign({}, this.state.formData);
      formData.timezone = this.state.consultData.timezone;
      formData.consultId = this.state.consultData.id;
      if(formData.appointmentTime !== ""){
        this.props.handleAcceptConsult(formData);
        this.onCancelAcceptReject();
      }else{
        this.props.showSnackbar(ConstantMessages.selectAppoinmentTime, Constants.SNACKBAR_VARIAINT.WARNING);
      }
    }else{
      this.props.showSnackbar(ConstantMessages.selectPreferenceMessage, Constants.SNACKBAR_VARIAINT.WARNING);
    }
  }

  handleRejectConsult = () => {
    const formData = Object.assign({}, this.state.formData);
    if(formData.reason !== ''){
      formData.timezone = this.state.consultData.timezone;
      formData.consultId = this.state.consultData.id;
      this.props.handleRejectConsult(formData);
      this.onCancelAcceptReject();
    }else{
      this.props.showSnackbar(ConstantMessages.selectReasonMessage, Constants.SNACKBAR_VARIAINT.WARNING);
    }
  }

  onReject = () => {
    this.setState({
      isReject: true
    })
  }

  onCancleReject = () => {
    this.setState({
      isReject: false
    })
  }

  onCancelAcceptReject = () => {
    this.setState({
      isReject: false,
      consultData: {},
      selectedPreference: ""
    }, () => {
      this.props.onCancel();
    })
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.visible !== nextProps.visible) {
      if(nextProps.consultData && nextProps.consultData.availableSlots){
        let consultData = Object.assign({}, nextProps.consultData);
        consultData.availableSlots = consultData.availableSlots.split(',');

        this.setState({
          consultData: consultData
        })
      }

    }
  }

  render() {
    let {visible} = this.props;
    const { consultData, formData } = this.state;

    return (
      <Dialog
        // fullScreen={fullScreen}
        fullWidth={true}
        open={visible}
        // onClose={this.onCancelAcceptReject}
        aria-labelledby="responsive-dialog-title"
      >
        <CustomeDialogTitle onClose={this.onCancelAcceptReject} id="responsive-dialog-title">Accept/Reject Consult</CustomeDialogTitle>
        <DialogContent>
          <ValidatorForm
            ref="form"
            onSubmit={this.state.isReject?() => this.handleRejectConsult():() => this.handleAcceptConsult()}
            noValidate
            onError={errors => console.log(errors)}
          >
            <Grid container spacing={0}>
              <Grid item xs={3}>
                <label className="formLabel">Patient Name: </label>
              </Grid>
              <Grid item xs={9}>
                <span className="formValue">{consultData.patientDetails?consultData.patientDetails.name:"-"}</span>
              </Grid>

              <Grid item xs={3}>
                <label className="formLabel">Date: </label>
              </Grid>
              <Grid item xs={9}>
                <span className="formValue">{moment(new Date(consultData.appointmentDate)).format("DD/MM/YYYY")}</span>
              </Grid>
              {
                !this.state.isReject
                ?(
                  <>
                    <Grid container spacing={3}>
                    {
                      consultData.availableSlots && consultData.availableSlots.map((preference) => {
                        return(
                          <Grid key={preference} item xs={4}>
                            <Button variant="outlined" color="primary" onClick={() => this.setState({ selectedPreference: preference })}
                              className={`preferences ${ this.state.selectedPreference===preference?"selectedPreference":"" }`}>
                              { preference }
                            </Button>
                          </Grid>
                        )
                      })
                    }
                    </Grid>
                    {
                    this.state.selectedPreference
                    ?(
                      <Grid item xs={12}>
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                          <TimePicker
                            margin="normal"
                            inputVariant="outlined"
                            id="time-picker"
                            className="appointmentTime"
                            label="Appointment Time"
                            value={formData.appointmentTime?formData.appointmentTime:null}
                            onChange={this.handleTimeChange}
                          />
                        </MuiPickersUtilsProvider>
                      </Grid>
                    )
                    :<></>
                    }
                  </>
                )
                :<></>
              }

            </Grid>

            {
              !this.state.isReject
              ?(
                <Grid container spacing={3}>
                  <Grid item xs={6}>
                    <Button variant="contained" color="secondary" onClick={() => this.onReject()}
                      className="invite-add-user-btn rejectButton">Reject</Button>
                  </Grid>
                  <Grid item xs={6}>
                    <Button type="submit" disabled={this.state.isReject} variant="contained" color="primary"
                      className="invite-add-user-btn acceptButton">Accept</Button>
                  </Grid>
                </Grid>
              )
              :<></>
            }

            {
            this.state.isReject
            ?(
              <Grid container spacing={1}>
                <Grid item xs={12}>
                  <TextValidator
                    select fullWidth
                    label="Reason" onChange={this.handleChange}
                    name="reason" value={formData.reason}
                    variant="outlined"
                    validators={['required']}
                    errorMessages={['this field is required']}
                  >
                    {
                      Constants.REJECT_REASON_LIST.map((rejectReason, index)=>{
                        return <MenuItem key={index} value={ rejectReason }>{ rejectReason }</MenuItem>
                      })
                    }
                  </TextValidator>
                </Grid>
                {
                  formData.reason === "Other"
                  ?(
                    <Grid item xs={12}>
                      <TextValidator
                        fullWidth
                        multiline
                        variant="outlined"
                        rows={3}
                        label="Reason Text" onChange={this.handleChange}
                        name="reasonText" value={formData.reasonText}
                        validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_AREA]}
                        errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_area]}
                      />
                    </Grid>
                  )
                  :<></>
                }
                <Grid item xs={6}>
                  <Button variant="outlined" color="primary" onClick={() => this.onCancleReject()}
                  className="invite-add-user-btn">Cancel</Button>
                </Grid>
                <Grid item xs={6}>
                  <Button type="submit" variant="contained" color="primary"
                  className="invite-add-user-btn rejectButton">Save Reason</Button>
                </Grid>
              </Grid>
            )
            :<></>
            }
          </ValidatorForm>
        </DialogContent>
      </Dialog>
    );
  }
}


