class RequestQueueConsults extends React.Component{

  constructor(props) {
    super(props);
    this.paginationParams = {
      page: 0,
      rowsPerPage: Constants.ROW_PER_PAGE_5,
    };
    this.state = {
      addNewConsultRequest: false,
      acceptRejectConsults: false,
      startRejectConsults: false,
      consultData: {},
      paginationParams: Object.assign({}, this.paginationParams)
    }
  };

  componentDidMount = () => {
    if(this.props.loggedInUserPractice){
      let data = this.state.paginationParams;
      this.props.getAllConsults(data);
    }
  }

  addNewConsultRequestDialog = (value) => {
    this.setState({
      addNewConsultRequest: value
    }, () => {
      if(!value){
        let data = this.state.paginationParams;
        this.props.getAllConsults(data);

        this.props.callBackCounts();
      }
    })
  }

  acceptRejectConsultDialog = (value, consultData) => {
    this.setState({
      acceptRejectConsults: value,
      consultData: consultData
    })
  }

  startRejectConsultDialog = (value, consultData) => {
    this.setState({
      startRejectConsults: value,
      consultData: consultData
    })
  }

  /**
   * Method to add new experiment
   */
  handleInvitePatient = (formData) => {
    const practiceId = this.props.loggedInUserPractice.PracticeId;
    const doctorId = this.props.loggedInUser.id;
    const timezone = this.props.loggedInUserPractice.Practice.timezone;
    formData.appointmentDate = moment(formData.appointmentDate).tz(timezone).format("YYYY/MM/DD");
    formData.appointmentTime = moment(formData.appointmentTime).tz(timezone).format("MM/DD/YYYY hh:mm A z");
    
    formData.practiceId=practiceId;
    formData.doctorId=doctorId;
    formData.timezone=timezone;

    this.props.addConsult(formData).then((res)=>{
      if(res){
        this.addNewConsultRequestDialog(false)
      }
    });
  };

  /**
   * Method to add new experiment
   */
  handleAcceptConsult = (formData) => {
      const timezone = this.props.loggedInUserPractice.Practice.timezone;
      formData.appointmentTime = moment(formData.appointmentTime).tz(timezone).format("MM/DD/YYYY hh:mm A z");
      this.props.confirmConsult(formData);

      this.acceptRejectConsultDialog(false, {});
      this.startRejectConsultDialog(false, {});
  };

  /**
   * Method to add new experiment
   */
  handleRejectConsult = (formData) => {
    this.props.rejectConsult(formData);

    this.acceptRejectConsultDialog(false, {});
    this.startRejectConsultDialog(false, {});
  };

  getPracticeDashboardSummary = (practiceId) => {
      this.props.getPracticeDashboardSummary(practiceId).then((res) => {
          if(res){

              this.setState({
                  pendingConsult: res.pendingConsult,
                  practiceUser: res.practiceUser,
                  revenue: res.revenue,
                  upcomingConsult: res.upcomingConsult
              })
          }
      })
  }

  getDoctorDashboardSummary = (practiceId) => {
      this.props.getDoctorDashboardSummary(practiceId).then((res) => {
          if(res){
              this.setState({
                  pendingConsult: res.pendingConsult,
                  practiceUser: 0,
                  revenue: res.revenue,
                  upcomingConsult: res.upcomingConsult
              })
          }
      })
  }

  render(){
      const {consults} = this.props;
      const { paginationParams } = this.state

      const columns = [{
        title: 'Type',
        maxWidth: 150,
        field: 'type',
        filterable: false,
        sorting: false,
        render: props => {
          if(props.isEmergency){
            return (
              <div className="isEmergency">
                  Emergency
              </div>
            )
          }else{
            return (
              <div className="">
                Normal
              </div>
            )
          }

        }
      }, {
        title: 'Name',
        field: 'name',
        filterable: false,
        sorting: false,
        render: props => {
          return (
            <span>
              { props.patientDetails?props.patientDetails.name:"-" }
            </span>
          )
        }
      }, {
        title: 'Preferences',
        fiels: 'availableSlots',
        filterable: false,
        sorting: false,
        render: props => {
          if(props.isEmergency){
            return (
              <span className="isEmergency">
                Now
              </span>
            )
          }else{
            return (
              <span>
                {props.availableSlots}
              </span>
            )
          }
        }
      }, 
      // {
      //   title: 'Payment',
      //   fiels: '',
      //   filterable: false,
      //   sorting: false,
      //   render: props => {
      //     return (
      //       props.paymentStatus
      //       ?(
      //         <span className="paymentStatus">
      //           { props.paymentStatus.data.status }
      //         </span>
      //       )
      //       :"-"
      //     )
      //   }
      // },
      {
        title: 'Action',
        maxWidth: 150,
        filterable: false,
        sorting: false,
        field: 'id',
        sortable: false,
        render: props => {
          // if(props.paymentStatus && props.paymentStatus.data.status !== "succeeded"){
            if(props.isEmergency){
              return (
  
                <span style={{ marginRight: 16 }}
                  className="spanLikeAnchor"
                  onClick={ () => this.startRejectConsultDialog(true, props) }>
                  Accept/Reject
                </span>
                // <Link to={`/consult/${props.id}`}>
                //   Start/Reject
                // </Link>
              )
            }else{
              return (
                <div className="">
                  {
                    props.status === Constants.CONSULTS_STATUS.PENDING
                    ?(
                      <span style={{ marginRight: 16 }}
                        className="spanLikeAnchor"
                        onClick={ () => this.acceptRejectConsultDialog(true, props) }>
                        Accept/Reject
                      </span>
                    )
                    :<></>
                  }
                </div>
              )
            }
          // }else{
          //   if(props.isEmergency){
          //     return (
  
          //       <span style={{ marginRight: 16 }}
          //         className="spanLikeAnchor"
          //         onClick={ () => this.startRejectConsultDialog(true, props) }>
          //         Accept/Reject
          //       </span>
          //       // <Link to={`/consult/${props.id}`}>
          //       //   Start/Reject
          //       // </Link>
          //     )
          //   }else{
          //     return (
          //       <div className="">
          //         {
          //           props.status === Constants.CONSULTS_STATUS.PENDING
          //           ?(
          //             <span style={{ marginRight: 16 }}
          //               className="spanLikeAnchor"
          //               onClick={ () => this.acceptRejectConsultDialog(true, props) }>
          //               Accept/Reject
          //             </span>
          //           )
          //           :<></>
          //         }
          //       </div>
          //     )
          //   }
          // }
        }
      }];

      return(
        <Grid container spacing={3}>
          <Grid item xs={12} className="informationHeader titleFontSize practiceOwnerInformation">
            Request Queue
            <Button className="alignRight" variant="contained" color="primary" onClick={() => this.addNewConsultRequestDialog(true)}>
              <PlusCircleOutlined className="marginRight10" /> Invite Patient
            </Button>

          </Grid>
          <Grid item xs={12}>
            <MaterialTable
              title=""
              scrollable
              columns={columns}
              data={consults.rows}
              options={{
                search: false,
                pageSizeOptions: [10, 20, 30, 40, 50],
                pageSize: paginationParams.rowsPerPage,
                emptyRowsWhenPaging: false,
                filtering: false,
                headerStyle: {
                  padding: '2px 40px 2px 16px'
                },
                searchFieldStyle: {
                  padding: '2px 40px 2px 16px'
                }
              }}
              pages={Math.ceil(consults.count / paginationParams.rowsPerPage)}
              totalCount={consults.count}
              page={paginationParams.page}
              onChangeRowsPerPage={(pageSize) => {
                let data = this.state.paginationParams;
                data['rowsPerPage'] = pageSize;
                this.setState({
                  paginationParams: data
                }, () => {
                  this.props.getAllConsults(data);
                })
              }}
              onChangePage={(page) => {
                let data = this.state.paginationParams;
                data['page'] = page;
                this.setState({
                  paginationParams: data
                }, () => {
                  this.props.getAllConsults(this.state.paginationParams);
                })
              }}
            />
          </Grid>

          <InvitePatientForm
            visible={this.state.addNewConsultRequest}
            onCreate={(formData) => this.handleInvitePatient(formData)}
            onCancel={() => this.addNewConsultRequestDialog(false)}
          />
          <AcceptRejectConsultForm
            visible={this.state.acceptRejectConsults}
            handleAcceptConsult={(formData) => this.handleAcceptConsult(formData)}
            handleRejectConsult={(formData) => this.handleRejectConsult(formData)}
            showSnackbar={(message, variant) => this.props.showSnackbar(message, variant)}
            consultData={this.state.consultData}
            onCancel={() => this.acceptRejectConsultDialog(false, {})}
          />

          <StartRejectConsultForm
            visible={this.state.startRejectConsults}
            handleAcceptConsult={(formData) => this.handleAcceptConsult(formData)}
            handleRejectConsult={(formData) => this.handleRejectConsult(formData)}
            showSnackbar={(message, variant) => this.props.showSnackbar(message, variant)}
            consultData={this.state.consultData}
            onCancel={() => this.startRejectConsultDialog(false, {})}
          />
        </Grid>
      )
  }

}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    consults: state.consults.consults,
    loggedInUserPractice: state.auth.loggedInUserPractice
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllConsults: (data) => {
      return dispatch(getAllConsultsRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    addConsult: (data) => {
      return dispatch(addConsultRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    confirmConsult: (data) => {
      return dispatch(confirmConsultRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    rejectConsult: (data) => {
      return dispatch(rejectConsultRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },


    getPracticeDashboardSummary: (practiceId) => {
      return dispatch(getPracticeSummary(practiceId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });

    },
    getDoctorDashboardSummary: (practiceId) => {
        return dispatch(getDoctorSummary(practiceId)).then((res) => {
          return Promise.resolve(res);
        }, (error) => {
          return Promise.reject(error);
        });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    }
  }
};

RequestQueueConsults = connect(
  mapStateToProps,
  mapDispatchToProps
)(RequestQueueConsults);

export default RequestQueueConsults;
