import {setPageTitle} from "../../DashboardActions";
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";

import Sidemenu from '../../../../utils/components/Sidemenu';
import UserDashboard from "../UserDashboard/UserDashboard";
import UserTable from "../../../User/components/UserTable/UserTable";
import UserProfile from "../../../User/components/UserProfile/UserProfile";
import PracticeProfile from '../../../Practice/components/PracticeProfile/PracticeProfile';
import UpcomingConsults from '../UserDashboard/component/UpcomingConsults';
import HistoricalConsults from '../UserDashboard/component/HistoricalConsults';
import SubscriptionPlan from '../../../SubscriptionPlans/components/SubscriptionPlans';

import {getUserPracticeRequest} from '../../../User/UserApiActions';

// const {Content, Footer, Header} = Layout;

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    minheight: '48px',
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
});

class Dashboard extends React.Component {

  constructor(props) {
    super(props);

    this.menu = this.props.menu;

    this.state = {
      noActivityDetected: false,
      showLoader: false,
      pathAllowed: false,
      openDrawer: false,
      practice: undefined,
    }
  }

  handleDrawerOpen = () => {
    this.setState({ openDrawer: true });
  };
  
  handleDrawerClose = () => {
    this.setState({ openDrawer: false })
  };

  /*
   * Check if the user is idle from last minutes, if so log out the user from the system
   */
  detectNoActivity = () => {
    let IDLE_TIMEOUT = 30; //minutes
    let _idleSecondsCounter = 0;

    const self = this;

    document.onclick = function () {
      _idleSecondsCounter = 0;
    };

    document.onmousemove = function () {
      _idleSecondsCounter = 0;
    };

    document.onkeypress = function () {
      _idleSecondsCounter = 0;
    };

    let interval = window.setInterval(CheckIdleTime, 60000);

    function CheckIdleTime() {

      _idleSecondsCounter++;
      if (_idleSecondsCounter >= IDLE_TIMEOUT) {
        self.setState({noActivityDetected: true});
        clearInterval(interval);
      }
    }

  };
  /**
   * Method executes on every menu click action
   */
  handleMenuClick = (menu) => {
    // If logout menu is clicked then trigger sign out action
    if (menu.key === 'logout') {
      this.logoutUser();
    } else {
      document.getElementsByClassName('ant-menu-submenu-title')[0].click();
      this.menu = menu.key;
    }
  };

  logoutUser = () => {
    this.refs.messageModal.setState({
      showModal: true, message: 'Are you sure you want to logout?', title: 'Log out',
      showOkButton: true,
      showCancelButton: true,
      handleOk: this.props.signOut,
      handleCancel: () => {
      },
      okText: 'Logout',
      cancelText: 'Cancel'
    });
  };

  componentDidMount() {
    // this.detectNoActivity();
    if(this.props.loggedInUser && !this.props.loggedInUserPractice.PracticeId){
      this.props.getUserPractice(this.props.loggedInUser.id).then((res) => {
        this.setState({ practice: res })
      });
    }
  }

  /**
   * Method used to close the snackbar
   */
  handleCloseSnackbar = () => {
    this.props.clearSnackbar();
  };

  render() {

    const {showLoader, openDrawer} = this.state;
    const {loggedInUser, loggedInUserPractice, currentRole, classes} = this.props;

    if(!loggedInUser){
      return (<Redirect to="/signin"/>)
    }else{
      if(!loggedInUserPractice){
        return (<Redirect to="/"/>)
      }else{
        if(currentRole.isPracticeAdmin && (!loggedInUserPractice.PracticeId || !loggedInUserPractice.Practice.stripeConnectAccountId || !loggedInUserPractice.Practice.stripeSubscriptionId)){
          return (<Redirect to="/setupPractice"/>)
        }else{
          return (
            <>
              <Router>
                <div className={classes.root}>
                  <CssBaseline />
                  <AppBar
                    position="fixed"
                    className={clsx(classes.appBar, {
                      [classes.appBarShift]: openDrawer,
                    })}
                  >
                    <Toolbar>
                      <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={() => this.handleDrawerOpen()}
                        edge="start"
                        className={clsx(classes.menuButton, openDrawer && classes.hide)}
                      >
                        <MenuIcon />
                      </IconButton>
                      <Typography variant="h6" noWrap className="sideMenuName">
                        { this.props.pageTitle }
                      </Typography>
                    </Toolbar>
                  </AppBar>
                  <Drawer
                    className={`${classes.drawer} sideDrawer`}
                    variant="persistent"
                    anchor="left"
                    open={openDrawer}
                    classes={{
                      paper: classes.drawerPaper,
                    }}
                  >
                    <div className={`${classes.drawerHeader} drawerHeaderContainer`}>
                      <IconButton onClick={() => this.handleDrawerClose()}>
                        <ChevronLeftIcon />
                        {/* {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />} */}
                      </IconButton>
                    </div>
  
                    <Sidemenu logoutUser={this.logoutUser} />
                  </Drawer>
                  <main
                    className={`${clsx(classes.content, {
                      [classes.contentShift]: openDrawer,
                    })} mainContainer`}
                  >
                    {
                      (showLoader
                      ?
                      (<div></div>)
                      : (
                        <Switch>
                          <Route path={ROUTE_ACTIONS.DASHBOARD} render={(props) => {
                            return <UserDashboard pageTitle='Dashboard'/>
                          }}/>
  
                          <Route exact path={ACTIONS.USER_LIST} render={(props) => {
                            return <UserTable {...props} pageTitle='Users'/>
                          }
                          }/>
  
                          <Route exact path={ACTIONS.UPCOMING_CONSULTS} render={(props) => {
                            return <UpcomingConsults {...props} pageTitle='Upcoming Consults'/>
                          }
                          }/>
  
                          <Route exact path={ACTIONS.HISTORICAL_CONSULTS} render={(props) => {
                            return <HistoricalConsults {...props} pageTitle='Historical Consults'/>
                          }
                          }/>
                          
                          <Route exact path={ACTIONS.SUBSCRIPTIONS} render={(props) => {
                            return <SubscriptionPlan {...props} pageTitle='Subscriptions'/>
                          }
                          }/>

                          <Route path={ACTIONS.PROFILE_VIEW} render={(props) => {
                            return <UserProfile {...props} pageTitle='Profile'/>
                          }}/>
  
                          <Route path={ACTIONS.PRACTICE_PROFILE_VIEW} render={(props) => {
                            return <PracticeProfile {...props} pageTitle='Practice Profile'/>
                          }
                          }/>
                        </Switch>
                      )
                    )
                    }
                  </main>
                </div>
              </Router>
  
              {/* <Modal
                visible={this.props.showSessionExpiredModal}
                title="Session Expired"
                onOk={this.clearSession}
                closable={false}
                footer={[
                  <Button key="submit" type="primary" onClick={this.clearSession}>
                    Ok
                  </Button>
                ]}>
                <p>{this.props.sessionExpiredError}</p>
              </Modal>
  
              <Modal
                visible={this.state.noActivityDetected}
                title="No Activity Detected"
                onOk={this.clearSession}
                closable={false}
                footer={[
                  <Button key="submit" type="primary" onClick={this.clearSession}>
                    Ok
                  </Button>
                ]}>
                <p>System has detected no activity in past 30 minutes. Please login again to continue.</p>
              </Modal> */}

              <Dialog
                maxWidth="sm"
                fullWidth={true}
                open={this.props.showSessionExpiredModal}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
              >
                <DialogTitle id="alert-dialog-title">Session Expired</DialogTitle>
                <DialogContent>
                  <DialogContentText id="alert-dialog-description">
                    { this.props.sessionExpiredError }
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button onClick={this.clearSession} color="primary" autoFocus>
                    Ok
                  </Button>
                </DialogActions>
              </Dialog>

              <Dialog
                maxWidth="sm"
                fullWidth={true}
                open={this.state.noActivityDetected}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
              >
                <DialogTitle id="alert-dialog-title">No Activity Detected</DialogTitle>
                <DialogContent>
                  <DialogContentText id="alert-dialog-description">
                    System has detected no activity in past 30 minutes. Please login again to continue.
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button onClick={this.clearSession} color="primary" autoFocus>
                    Ok
                  </Button>
                </DialogActions>
              </Dialog>
  
              <MessageModal
                ref="messageModal"/>
  
              <AppLoader showLoader={this.state.showLoader}/>

              <Snackbar open={this.props.showSnackbar} autoHideDuration={3000}
                        onClose={() => this.handleCloseSnackbar()}
                        anchorOrigin={{vertical: 'top', horizontal: 'right'}}>
                <Alert severity={this.props.snackbarVariant}>
                  {this.props.snackbarMessage}
                </Alert>
              </Snackbar>
            </>
          )
        }
      }
    }
    
    // return (
    //   !loggedInUser
    //     ? (<Redirect to="/signin"/>)
    //     : loggedInUserPractice && !loggedInUserPractice.PracticeId
    //     ?(<Redirect to="/" />)
    //     : loggedInUserPractice && loggedInUserPractice.PracticeId
    //     ?<></>
    //     :<></>
    // );
  }

  clearSession() {
    Auth.logoutAndNavigateToLogin();
  }

}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    pageTitle: state.dashboard.pageTitle,
    secondaryTitle: state.dashboard.secondaryTitle,
    showSessionExpiredModal: state.auth.showSessionExpiredModal,
    sessionExpiredError: state.auth.sessionExpiredError,
    loggedInUserPractice: state.auth.loggedInUserPractice,
    currentRole: state.auth.currentRole,
    showSnackbar: state.showLoader.showSnackbar,
    snackbarVariant: state.showLoader.snackbarVariant,
    snackbarMessage: state.showLoader.snackbarMessage
  }
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    signOut: () => {
      dispatch(signOut())
    },

    updatePageTitle: (pageTitle, enablePageTitle) => {
      dispatch(setPageTitle(pageTitle, enablePageTitle));
    },
    getUserPractice: (userId) => {
      return dispatch(getUserPracticeRequest(userId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    clearSnackbar: () => {
      dispatch(clearSnackbar())
    },

  }
};

Dashboard = connect(
  mapStateToProps,
  mapDispatchToProps
)(Dashboard);

export default withStyles(styles)(Dashboard);